<template>
  <div id="aboutus">
    <common-header :title="title"></common-header>
    <div class="content" v-html="content">
    	
    </div>
  </div>
</template>

<script type="text/javascript">
	export default {
		data(){
			return{
				content:'',
				title:this.$route.query.title,
			}
		},
		created(){
			this.getArticleDetails()
		},
		methods:{
			getArticleDetails(){
				let id = this.$route.query.id
				this.$Api.getArticleDetails(id).then((res)=>{
					if(res.q.s == 0){
						this.content = res.q.article.content
					}
				})
			}
		},
	}
</script>

<style lang="scss">
	#aboutus{
		font-size: 0.4rem;
		padding: 0.4rem;
	}
</style>


